* {
  margin: 0;
  padding: 0; }

html, body, #root {
  height: 100%;
  width: 100%; }

.App {
  text-align: center;
  background: black;
  color: white;
  height: 100%; }

.App.timerActive {
  background: #f7f7f7; }

.App-logo {
  height: 80px; }

.App-logo.animate {
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.App-header {
  background-color: #3b5998;
  height: 150px;
  padding: 20px;
  color: white;
  font-size: 1.5em; }

.App-intro {
  font-size: 15em;
  margin: 0; }

.App-intro.down {
  color: red; }

.App-intro.down.ready {
  color: green; }

.App.timerActive .App-intro,
.App.timerActive .timelist,
.App.timerActive .stats {
  color: black; }

.time {
  font-family: Courier; }

.timelist, .stats {
  font-size: 1.5em; }

.timelist {
  padding: 0 40%; }

.stats {
  list-style: none;
  padding: 0;
  height: 60px;
  width: 100%; }

.stats li {
  float: left;
  width: 25%; }

@media only screen and (max-width: 1024px) {
  h2 {
    margin: 0; }
  .App-logo {
    height: 40px; }
  .App-header {
    height: 60px;
    font-size: .8em; }
  .App-intro {
    font-size: 7em; }
  .timelist {
    padding: 0 40%; } }

@media only screen and (max-width: 667px) {
  .App-header {
    height: 90px;
    padding: 10px 20px; }
  .App-intro {
    font-size: 4em; }
  .stats {
    height: 275px; }
  .stats li {
    float: none;
    width: auto;
    margin-bottom: 10px; }
  .timelist {
    padding: 0 25%; }
  * {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Chrome/Safari/Opera */
    /* Konqueror */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none; } }



